.request {
  padding-bottom: 168px;
}

.request__container {
  display: grid;
}

.request__wrap {
  position: relative;

  display: grid;
  justify-self: end;

  width: 100%;
  max-width: 690px;
}

.request__wrap::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -671px;
  z-index: -1;

  width: 671px;
  height: 445px;
  margin-top: 20px;

  background-image: url("../img/request-dc.jpg");
  background-repeat: no-repeat;

  transform: translateY(-50%);
}

.request__text {
  margin: 0;
  margin-bottom: 40px;

  font-size: 28px;
}

.request__text b {
  font-weight: 700;
}

.request__popup-open {
  width: 100%;
  max-width: 450px;
  padding: 30px;
}

@media (max-width: 1399px) {
  .request {
    padding-bottom: 100px;
  }

  .request__wrap {
    justify-items: center;

    max-width: 100%;
  }

  .request__wrap::before {
    top: -380px;
    left: 50%;

    margin-top: 0;

    transform: translateX(-50%);
  }

  .request__text {
    text-align: center;
  }
}

@media (max-width: 799px) {
  .request {
    padding-bottom: 66px;
  }
}

@media (max-width: 579px) {
  .request__wrap::before {
    top: -270px;

    width: 392px;
    height: 260px;

    background-size: cover;
  }

  .request__text {
    font-size: 24px;
  }
}

@media (max-width: 459px) {
  .request__popup-open {
    padding: 16px;

    font-size: 20px;
  }
}
