.partners {
  margin-bottom: 120px;
}

@media (max-width: 479px) {
  .partners {
    margin-bottom: 60px;
  }
}

.partners__container {
  display: grid;
  justify-items: center;

  width: calc(100% - 40px);
  max-width: 1400px;
  margin: 0 auto;
  padding: 75px 0;
  border-bottom: 10px solid #8abaf1;
  border-radius: 4px;

  background-color: #fff;
  box-shadow: 0 11px 131px 5px rgba(0, 0, 0, .16);
}

@media (max-width: 499px) {
  .partners__container {
    width: 100%;
  }
}

@media (max-width: 479px) {
  .partners__container {
    padding: 40px 0;
  }
}

.partners__title {
  margin: 0;
  margin-bottom: 60px;
  padding: 0 20px;

  font-weight: 700;
  font-size: 48px;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: 579px) {
  .partners__title {
    margin-bottom: 40px;

    font-size: 32px;
  }
}

@media (max-width: 479px) {
  .partners__title {
    margin-bottom: 20px;

    font-size: 24px;
  }
}

.partners__text {
  max-width: 1000px;
  margin: 0;
  margin-bottom: 40px;
  padding: 0 20px;

  font-size: 28px;
  text-align: center;
}

@media (max-width: 579px) {
  .partners__text {
    font-size: 24px;
  }
}

@media (max-width: 479px) {
  .partners__text {
    font-size: 18px;
  }
}

.partners__wrap {
  position: relative;

  display: grid;

  justify-content: center;

  width: 100%;
}

.partners__swiper-container {
  position: relative;

  overflow: hidden;

  width: 100%;
  max-width: 1120px;
}

@media (max-width: 1279px) {
  .partners__swiper-container {
    max-width: 932px;
  }
}

@media (max-width: 1099px) {
  .partners__swiper-container {
    max-width: 744px;
  }
}

@media (max-width: 899px) {
  .partners__swiper-container {
    max-width: 556px;
  }
}

@media (max-width: 699px) {
  .partners__swiper-container {
    max-width: 368px;
  }
}

@media (max-width: 499px) {
  .partners__swiper-container {
    max-width: 180px;
  }
}

.partners__swiper-wrapper {
  display: grid;
  grid-template-columns: repeat(100, 180px);
  grid-column-gap: 8px;

  margin: 0;
  padding: 0;

  list-style: none;
}

.partners__swiper-buttons {
  position: absolute;
  top: 50%;
  left: -30px;

  display: grid;
  grid-template-columns: repeat(2, 60px);
  justify-content: space-between;

  width: 100vw;
  max-width: 1460px;

  pointer-events: none;

  transform: translateY(-50%);
}

@media (max-width: 1479px) {
  .partners__swiper-buttons {
    left: -20px;
  }
}

@media (max-width: 509px) {
  .partners__swiper-buttons {
    grid-template-columns: repeat(2, 40px);
  }
}

@media (max-width: 499px) {
  .partners__swiper-buttons {
    left: 50%;

    width: calc(100vw - 60px);

    transform: translate(-50%, -50%);
  }
}

@media (max-width: 359px) {
  .partners__swiper-buttons {
    width: calc(100vw - 24px);
  }
}

.partners__swiper-button {
  position: relative;

  width: 60px;
  height: 60px;

  padding: 0;
  border: none;
  border-radius: 50%;

  background-color: transparent;
  box-shadow: 0 8px 44px 2px rgba(0, 0, 0, .31);

  cursor: pointer;
  pointer-events: visible;
}

@media (max-width: 509px) {
  .partners__swiper-button {
    width: 40px;
    height: 40px;
  }
}
