.error {
  margin-bottom: 100px;
}

.error__container {
  display: grid;
  justify-items: center;

  text-align: center;
}

.error__container picture {
  margin-bottom: 12px;
}

@media (max-width: 669px) {
  .error__container picture {
    max-width: 320px;
  }
}

.error__title {
  margin: 0;
  margin-bottom: 28px;

  font-weight: 700;
  font-size: 48px;
  text-transform: uppercase;
}

@media (max-width: 669px) {
  .error__title {
    font-size: 28px;
  }
}

.error__text {
  max-width: 930px;
  margin: 0;
  margin-bottom: 50px;

  font-size: 28px;
}

@media (max-width: 669px) {
  .error__text {
    font-size: 24px;
  }
}

.error__container a {
  color: #2a85ec;

  font-size: 28px;
}

@media (max-width: 669px) {
  .error__container a {
    font-size: 24px;
  }
}
