.service {
  position: relative;

  padding: 100px 0 132px;
}

.service::before {
  content: "";
  position: absolute;
  top: 110px;
  left: 50%;
  z-index: -1;

  width: 942px;
  height: 716px;

  background-image: url("../img/service-bg-dc.png");
  background-repeat: no-repeat;

  transform: translateX(-50%);
}

.service__container {
  display: grid;
  justify-items: center;
}

.service__lead {
  margin: 0;
  margin-bottom: 48px;

  font-weight: 700;
  font-size: 48px;
  text-align: center;
  text-transform: uppercase;
}

.service__text {
  max-width: 1010px;
  margin: 0;
  margin-bottom: 70px;

  font-size: 28px;
  text-align: center;
}

.service__list-text {
  margin: 0;
  margin-bottom: 110px;

  font-weight: 700;
  font-size: 36px;
  text-align: center;
  text-transform: uppercase;
}

.service__list {
  display: grid;
  grid-auto-flow: column;
  grid-column-gap: 20px;
  justify-content: space-between;

  width: 100%;

  margin: 0;
  margin-bottom: 50px;
  padding: 0;

  list-style: none;
}

.service__item {
  position: relative;

  display: grid;

  width: 100%;
  max-width: 248px;
  padding-top: 180px;
}

.service__item picture {
  position: absolute;
  left: 50%;

  justify-content: center;

  width: 149px;

  pointer-events: none;

  transform: translateX(-50%);
}

.service__item-text {
  margin: 0;

  font-size: 28px;
  text-align: center;
}

.service__item-text b {
  font-weight: 700;
  text-transform: uppercase;
}

.service__link {
  max-width: 450px;
  padding: 30px;
}

@media (max-width: 1399px) {
  .service {
    padding-bottom: 360px;
  }

  .service::before {
    top: auto;
    bottom: 260px;
  }

  .service__lead {
    margin-bottom: 40px;

    font-size: 40px;
  }

  .service__text {
    max-width: 920px;
    margin-bottom: 40px;
  }

  .service__list-text {
    margin-bottom: 50px;
  }

  .service__list {
    grid-template-columns: 260px 120px 120px 260px;
    grid-auto-flow: row;
    grid-row-gap: 45px;

    margin-bottom: 60px;
  }

  .service__item {
    max-width: 260px;
  }
}

@media (min-width: 860px) and (max-width: 1399px) {
  .service__item:nth-child(1) {
    grid-column: 1 / 2;
  }

  .service__item:nth-child(2) {
    grid-column: 2 / 4;
    justify-self: center;
  }

  .service__item:nth-child(3) {
    grid-column: 4 / 5;
  }

  .service__item:nth-child(4) {
    grid-row-start: 2;
    grid-column: 1 / 3;
    justify-self: end;
  }

  .service__item:nth-child(5) {
    grid-row-start: 2;
    grid-column: 3 / 5;
    justify-self: start;
  }
}

@media (max-width: 859px) {
  .service::before {
    top: 380px;

    width: 458px;
    height: 348px;

    background-size: cover;
  }

  .service__list {
    grid-template-columns: 260px 260px;
    justify-content: space-around;
  }
}

@media (max-width: 579px) {
  .service {
    padding-bottom: 290px;
  }

  .service__lead {
    margin-bottom: 40px;

    font-size: 36px;
  }

  .service__text {
    margin-bottom: 48px;

    font-size: 24px;
  }

  .service__list-text {
    margin-bottom: 74px;

    font-size: 32px;
  }

  .service__list {
    grid-template-columns: 1fr;
    grid-row-gap: 100px;
  }

  .service__item {
    max-width: 100%;
    padding-top: 0;
    padding-left: 174px;

    text-align: left;
  }

  .service__item picture {
    top: 50%;
    left: 0;

    justify-content: start;

    transform: translateX(0) translateY(-50%);
  }

  .service__item-text {
    font-size: 24px;
    text-align: left;
  }
}

@media (max-width: 479px) {
  .service__lead {
    margin-bottom: 24px;

    font-size: 24px;
  }

  .service__text {
    margin-bottom: 32px;

    font-size: 18px;
  }

  .service__list-text {
    margin-bottom: 40px;

    font-size: 20px;
  }

  .service__list {
    grid-row-gap: 60px;
  }

  .service__item {
    padding-left: 96px;
  }

  .service__item picture {
    width: 80px;
  }

  .service__item-text {
    font-size: 20px;
  }
}

@media (max-width: 459px) {
  .service__link {
    padding: 16px;

    font-size: 20px;
  }
}

@media (max-width: 459px) {
  .service {
    padding-top: 40px;
  }
}
