.footer {
  padding: 26px 0;

  background-color: #212121;
}

.footer__container {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-row-gap: 36px;
  grid-column-gap: 20px;
  align-items: center;
  justify-content: space-between;
}

.footer__copyright {
  display: grid;
  grid-template-columns: max-content max-content max-content;
  grid-column-gap: 20px;
  align-items: center;

  color: #fff.6;

  font-size: 24px;
}

.footer__copyright p {
  margin: 0;
}

.footer__copyright a {
  color: unset;
}

.footer__email {
  position: relative;

  padding-left: 44px;

  color: #fff;

  font-size: 28px;
  text-decoration: none;
}

.footer__email::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;

  width: 28px;
  height: 20px;

  background-image: url("../img/email.svg");
  background-repeat: no-repeat;

  transform: translateY(-50%);
}

@media (max-width: 1123px) {
  .footer__container {
    grid-template-columns: 1fr;
    justify-items: center;
  }

  .footer__email {
    grid-row-start: 1;
  }
}

@media (max-width: 767px) {
  .footer__copyright {
    grid-template-columns: max-content;
    grid-row-gap: 12px;
    justify-items: center;
  }

  .footer__copyright span {
    display: none;
  }
}

@media (max-width: 479px) {
  .footer__copyright p,
  .footer__copyright a {
    font-size: 16px;
  }

  .footer__email {
    font-size: 16px;
  }
}
