.contacts {
  padding-top: 68px;
  padding-bottom: 90px;
}

.contacts__container {
  display: grid;
  justify-items: center;
}

.contacts__title {
  margin: 0;
  margin-bottom: 44px;

  font-weight: 700;
  font-size: 48px;
  text-transform: uppercase;
}

.contacts__text {
  max-width: 760px;
  margin: 0;
  margin-bottom: 32px;

  font-size: 28px;
  text-align: center;
}

.contacts__text a {
  color: unset;

  font-weight: 700;
  text-decoration: none;
}

.contacts__text button {
  padding: 0;
  border: none;

  background-color: transparent;
  color: #2a85ec;

  text-decoration: underline;

  cursor: pointer;
}

.contacts__list-text {
  margin: 0;
  margin-bottom: 32px;

  font-size: 28px;
  text-align: center;
}

.contacts__messangers {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  grid-column-gap: 16px;

  margin: 0;
  margin-bottom: 50px;
  padding: 0;

  list-style: none;
}

.contacts__messanger {
  display: grid;
}

.contacts__popup-open {
  max-width: 450px;
  margin-bottom: 54px;
  padding: 30px 16px;
}

.contacts__social-title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 700;
  font-size: 36px;
  text-align: center;
  text-transform: uppercase;
}

.contacts__social-text {
  margin: 0;
  margin-bottom: 46px;

  font-size: 28px;
  text-align: center;
}

.contacts__socials {
  display: grid;
  grid-template-columns: repeat(3, 210px);
  grid-column-gap: 30px;

  margin: 0;
  padding: 0;

  list-style: none;
}

.contacts__social a {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-column-gap: 20px;
  align-items: center;

  height: 48px;
  border-radius: 4px;

  color: #fff;

  font-size: 28px;
  text-decoration: none;
}

.contacts__social:nth-child(1) a {
  padding-left: 9px;

  background-color: #3c5a9a;
}

.contacts__social:nth-child(2) a {
  padding-left: 16px;

  background: linear-gradient(to right, #ffd600 0%, #f40713 48%, #7b38ab 100%);
}

.contacts__social:nth-child(3) a {
  padding-left: 25px;

  background-color: #fe011c;
}

@media (max-width: 739px) {
  .contacts__socials {
    grid-template-columns: 1fr;
    grid-row-gap: 24px;

    width: 100%;
    max-width: 210px;
  }
}

@media (max-width: 579px) {
  .contacts {
    padding-top: 50px;
    padding-bottom: 64px;
  }

  .contacts__title {
    margin-bottom: 28px;

    font-size: 32px;
  }

  .contacts__text {
    font-size: 24px;
  }

  .contacts__messangers {
    margin-bottom: 30px;
  }

  .contacts__social-title {
    font-size: 32px;
  }

  .contacts__social-text {
    font-size: 24px;
  }
}

@media (max-width: 479px) {
  .contacts__popup-open {
    padding: 16px;

    font-size: 20px;
  }

  .contacts__title {
    margin-bottom: 20px;

    font-size: 24px;
  }

  .contacts__text {
    font-size: 20px;
  }

  .contacts__list-text {
    font-size: 20px;
  }

  .contacts__social-title {
    font-size: 24px;
  }

  .contacts__social-text {
    font-size: 20px;
  }
}

@media (max-width: 359px) {
  .contacts__popup-open {
    font-size: 16px;
  }
}
