.preview {
  position: relative;

  padding: 72px 0 64px;
}

.preview::before {
  content: "";
  position: absolute;
  top: -10px;
  left: 50%;
  z-index: -1;

  overflow: hidden;

  width: 100%;
  min-width: 1936px;
  height: 100%;
  min-height: 100%;

  background-image: url("../img/preview-bg-desktop.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  transform: translateX(-50%);
}

.preview__container {
  display: grid;
}

.preview__wrap {
  display: grid;
  justify-self: end;

  width: 100%;
  max-width: 940px;
}

.preview__wrap h2 {
  max-width: 860px;
  margin: 0;
  margin-bottom: 50px;

  font-weight: 700;
  font-size: 52px;
  text-transform: uppercase;
}

.preview__wrap h2 span {
  font-size: 36px;
}

.preview__wrap ul {
  display: grid;
  grid-row-gap: 20px;

  max-width: 860px;

  margin: 0;
  margin-bottom: 50px;
  padding: 0;

  list-style: none;
}

.preview__wrap li {
  position: relative;

  padding-left: 52px;

  font-weight: 500;
  font-size: 28px;
}

.preview__wrap li::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;

  width: 36px;
  height: 28px;

  background-image: url("../img/preview-item-marker.png");
  background-repeat: no-repeat;
}

.preview__wrap p {
  max-width: 860px;
  margin: 0;
  margin-bottom: 40px;

  font-weight: 700;
  font-size: 28px;
}

.preview__popup-open {
  max-width: 450px;
  padding: 30px;
}

@media (max-width: 1023px) {
  .preview {
    padding: 36px 0 26px;
  }

  .preview::before {
    top: 0;

    min-width: 1021px;

    background-image: url("../img/preview-bg-tablet.jpg");
  }

  .preview__wrap {
    max-width: 600px;
  }

  .preview__wrap h2 {
    margin-bottom: 36px;

    font-size: 40px;
  }

  .preview__wrap h2 span {
    font-size: 28px;
  }

  .preview__wrap ul {
    margin-bottom: 46px;
  }

  .preview__wrap li {
    font-size: 20px;
  }

  .preview__wrap li::before {
    top: 0;
  }

  .preview__wrap p {
    max-width: 480px;
    margin-bottom: 30px;

    font-size: 20px;
  }
}

@media (max-width: 539px) {
  .preview {
    padding: 284px 0 20px;
  }

  .preview::before {
    top: 0;

    min-width: 581px;
    height: 355px;
    min-height: auto;

    background-image: url("../img/preview-bg-mobile.png");
  }

  .preview__wrap h2 {
    margin-bottom: 32px;

    font-size: 36px;
  }

  .preview__wrap h2 span {
    font-size: 24px;
  }

  .preview__wrap ul {
    grid-row-gap: 16px;

    margin-bottom: 32px;
  }

  .preview__wrap li {
    font-size: 20px;
  }

  .preview__wrap li::before {
    top: 0;
  }

  .preview__wrap p {
    max-width: 480px;
    margin-bottom: 30px;

    font-size: 20px;
  }
}

@media (max-width: 459px) {
  .preview__wrap h2 {
    font-size: 24px;
  }

  .preview__wrap h2 span {
    font-size: 16px;
  }

  .preview__wrap li {
    font-size: 16px;
  }

  .preview__wrap p {
    font-size: 16px;
  }

  .preview__popup-open {
    padding: 16px;

    font-size: 20px;
  }
}
