.check {
  padding: 128px 0;
}

@media (max-width: 479px) {
  .check {
    padding: 64px 0;
  }
}

.check__container {
  display: grid;
  grid-template-columns: minmax(280px, 388px) 1fr;
  grid-column-gap: 20px;
  align-items: center;
}

.check__wrap {
  position: relative;
  z-index: 3;

  display: grid;
  grid-row-start: 1;
  grid-column-start: 2;
  justify-self: end;

  width: 100%;
  max-width: 810px;
}

.check__wrap h2 {
  margin: 0;
  margin-bottom: 54px;

  font-weight: 700;
  font-size: 48px;
  text-transform: uppercase;
}

.check__wrap blockquote {
  display: grid;

  margin: 0;
  margin-bottom: 70px;
}

.check__wrap blockquote p {
  margin: 0;

  font-size: 28px;
}

.check__wrap blockquote cite {
  padding-top: 32px;

  font-size: 28px;

  opacity: .6;
}

.ckeck__file {
  position: relative;

  max-width: 655px;
  margin: 0;
  padding-left: 85px;

  font-size: 28px;
}

.ckeck__file a {
  color: #2a85ec;
}

.ckeck__file::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;

  width: 72px;
  height: 72px;

  background-image: url("../img/check-file.png");
  background-repeat: no-repeat;

  transform: translateY(-50%);
}

.check__photo-wrap {
  position: relative;

  grid-row-start: 1;
  grid-column-start: 1;
}

.check__photo-wrap::before {
  content: "";
  position: absolute;
  top: -62px;
  left: -20px;
  z-index: 0;

  width: 559px;
  height: 400px;

  background-image: url("../img/check-dc-before.svg");
  background-repeat: no-repeat;
}

.check__photo-wrap::after {
  content: "";
  position: absolute;
  right: -39px;
  bottom: -49px;
  z-index: 2;

  width: 308px;
  height: 136px;

  background-image: url("../img/check-dc-after.svg");
  background-repeat: no-repeat;
}

.check__photo-wrap img {
  position: relative;
  z-index: 1;

  width: 100%;
  max-width: 388px;
  height: 388px;
  border-radius: 4px;

  box-shadow: 0 24px 44px 2px rgba(0, 0, 0, .19);
}

@media (max-width: 1299px) {
  .ckeck {
    padding: 120px 0 80px;
  }

  .check__container {
    grid-template-columns: 1fr;
  }

  .check__wrap {
    grid-row-start: 2;
    grid-column: 1/-1;
    justify-self: center;

    max-width: 915px;
  }

  .check__photo-wrap {
    justify-self: center;

    margin-bottom: 123px;
  }

  .check__wrap h2 {
    text-align: center;
  }

  .check__wrap blockquote {
    text-align: center;
  }

  .ckeck__file {
    justify-self: center;
  }
}

@media (max-width: 1023px) {
  .check__wrap h2 {
    margin-bottom: 40px;

    font-size: 40px;
  }
}

@media (max-width: 579px) {
  .check__wrap h2 {
    font-size: 32px;
  }

  .check__wrap blockquote p {
    font-size: 24px;
  }

  .check__wrap blockquote cite {
    font-size: 24px;
  }

  .ckeck__file {
    font-size: 24px;
  }

  .check__photo-wrap {
    margin-bottom: 80px;
  }
}

@media (max-width: 479px) {
  .check__wrap h2 {
    margin-bottom: 20px;

    font-size: 24px;
  }

  .check__wrap blockquote {
    margin-bottom: 32px;
  }

  .check__wrap blockquote p {
    font-size: 18px;
  }

  .check__wrap blockquote cite {
    font-size: 18px;
  }

  .ckeck__file {
    font-size: 18px;
  }

  .check__photo-wrap {
    margin-bottom: 40px;
  }
}
