.header {
  box-shadow: 0 0 19px 1px rgba(0, 0, 0, .14);
}

@media (min-width: 1024px) {
  .header__nav-wrap {
    background-color: #212121;
  }

  .header__wrap {
    padding: 28px 0;
  }

  .header__open {
    display: none;
  }

  .header__container {
    display: grid;
    grid-template-columns: max-content 1fr;
    grid-column-gap: 20px;
    align-items: center;
    justify-content: space-between;
  }
}

.header__logo {
  display: grid;
}

.header__contacts {
  display: grid;
  grid-column-gap: 20px;
  align-items: center;
  justify-content: space-between;
  justify-self: end;

  width: 100%;
}

@media (min-width: 1024px) {
  .header__contacts {
    grid-template-columns: max-content max-content;

    max-width: 450px;
  }
}

.header__contacts-toggle {
  display: none;
}

.header__phone {
  color: #000;

  font-weight: 700;
  font-size: 28px;
  line-height: normal;
  text-decoration: none;
  text-transform: uppercase;
}

.header__messangers {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  grid-column-gap: 16px;

  margin: 0;
  padding: 0;

  list-style: none;
}

.header__messanger {
  display: grid;
}

@media (max-width: 1023px) {
  .header {
    padding: 8px 0;
  }

  .header__container {
    position: relative;

    display: grid;
    grid-template-columns: minmax(max-content, 100px) max-content minmax(max-content, 1fr);
    grid-column-gap: 20px;
  }

  .header__open {
    align-self: center;
    justify-self: start;

    width: 37px;
    height: 26px;
    padding: 0;
    border: none;

    background-color: transparent;

    cursor: pointer;
  }

  .header__messangers {
    justify-content: space-between;
    justify-self: end;

    width: 100%;
    max-width: 170px;
  }
}

@media (min-width: 800px) and (max-width: 1023px) {
  .header__contacts {
    grid-template-columns: max-content minmax(max-content, 1fr);

    max-width: 500px;
  }
}

@media (max-width: 799px) {
  .header__container {
    grid-template-columns: max-content 1fr max-content;
    justify-content: space-between;
  }

  .header__logo {
    justify-self: center;
  }

  .header__contacts-toggle {
    position: relative;
    z-index: 3;

    display: grid;
    justify-content: center;

    width: 38px;

    padding: 0;
    border: none;

    background-color: transparent;

    cursor: pointer;
  }

  .header__phone {
    display: none;
  }

  .header__contacts-toggle picture {
    pointer-events: none;
  }

  .header__contacts-toggle--closed .header__contact-close,
  .header__contacts-toggle--opened .header__contact-open {
    display: none;
  }

  .header__contacts-toggle--closed .header__contact-open,
  .header__contacts-toggle--opened .header__contact-close {
    display: flex;
  }

  .header__messangers--closed {
    display: none;
  }

  .header__messangers--opened {
    position: absolute;
    top: -8px;
    right: 0;
    z-index: 2;

    align-items: center;

    min-width: 320px;

    height: calc(100% + 16px);
    padding-right: 96px;
    padding-left: 20px;

    background-color: #212121;
  }
}
