.advantages {
  margin-bottom: 120px;
}

@media (max-width: 479px) {
  .advantages {
    margin-bottom: 60px;
  }
}

.advantages__container {
  display: grid;
  justify-items: center;
}

.advantages__lead {
  max-width: 940px;
  margin: 0;
  margin-bottom: 40px;

  font-weight: 700;
  font-size: 48px;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: 579px) {
  .advantages__lead {
    font-size: 36px;
  }
}

@media (max-width: 479px) {
  .advantages__lead {
    margin-bottom: 20px;

    font-size: 24px;
  }
}

.advantages__text {
  max-width: 1160px;
  margin: 0;
  margin-bottom: 100px;

  font-size: 28px;
  text-align: center;
}

@media (max-width: 579px) {
  .advantages__text {
    margin-bottom: 60px;

    font-size: 24px;
  }
}

@media (max-width: 479px) {
  .advantages__text {
    margin-bottom: 30px;

    font-size: 18px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: repeat(4, 314px);
  grid-column-gap: 20px;
  justify-content: space-between;

  width: 100%;
  margin: 0;
  padding: 0;

  list-style: none;
}

@media (max-width: 1355px) {
  .advantages__list {
    grid-template-columns: repeat(2, 314px);
    grid-row-gap: 40px;
    justify-content: space-around;
  }
}

@media (max-width: 687px) {
  .advantages__list {
    grid-template-columns: minmax(280px, 314px);
  }
}

.advantages__item {
  display: grid;
  align-content: start;
  justify-items: center;

  font-size: 26px;

  text-align: center;
}

@media (max-width: 479px) {
  .advantages__item {
    font-size: 24px;
  }
}

.advantages__item picture {
  height: 120px;
  margin-bottom: 40px;
}

@media (max-width: 479px) {
  .advantages__item picture {
    margin-bottom: 20px;
  }
}

.advantages__item h3 {
  margin: 0;
  margin-bottom: 40px;

  font-weight: 700;
  text-transform: uppercase;
}

@media (max-width: 479px) {
  .advantages__item h3 {
    margin-bottom: 20px;
  }
}

.advantages__item p {
  margin: 0;
}
