.popup {
  position: absolute;
  z-index: 100;

  width: 100%;
  min-width: 320px;
  height: 100%;

  background-color: #212121.5;
}

.popup__wrap {
  position: fixed;
  top: 50vh;
  left: 50%;

  display: grid;
  grid-row-gap: 12px;

  overflow-y: auto;

  width: calc(100vw - 40px);
  max-width: 600px;
  max-height: 100%;
  padding: 16px 16px 32px;
  border-radius: 4px;

  background-color: #fff;

  transform: translate(-50%, -50%);
}

@media (max-width: 599px) {
  .popup__wrap {
    padding: 16px 16px 24px;
  }
}

.popup__close {
  position: relative;

  display: flex;
  align-items: center;

  justify-self: end;

  padding: 0;
  border: none;

  background-color: transparent;

  cursor: pointer;
}

.popup__before {
  display: grid;
}

.popup__title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;

  font-size: 32px;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: 599px) {
  .popup__title {
    font-size: 24px;
  }
}

.popup__text {
  margin: 0;
  margin-bottom: 40px;

  font-size: 24px;

  text-align: center;
}

@media (max-width: 599px) {
  .popup__text {
    margin-bottom: 20px;

    font-size: 16px;
  }
}

.popup__form {
  display: grid;
  grid-row-gap: 16px;
  justify-self: center;

  width: 100%;
  max-width: 360px;
}

@media (max-width: 599px) {
  .popup__form {
    grid-row-gap: 8px;

    max-width: 220px;
  }
}

.popup__input {
  width: 100%;
  padding: 16px;
  border: 1px solid #212121.5;
  border-radius: 4px;

  color: #212121;

  font-size: 24px;
  line-height: 1;

  transition: .2s;
}

@media (max-width: 599px) {
  .popup__input {
    padding: 8px;

    font-size: 16px;
  }
}

.popup__input::placeholder {
  color: #212121.5;
}

.popup__input:hover,
.popup__input:focus {
  border: 1px solid #ffb200;

  outline: none;
}

.popup__send {
  margin-top: 20px;
  padding: 16px;

  font-size: 28px;
}

@media (max-width: 599px) {
  .popup__send {
    padding: 12px;

    font-size: 20px;
  }
}

.popup__after {
  display: grid;
}

.popup__after-title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;

  font-size: 32px;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: 599px) {
  .popup__after-title {
    font-size: 23px;
  }
}

.popup__after-text {
  justify-self: center;

  max-width: 360px;
  margin: 0;
  margin-bottom: 60px;

  font-size: 24px;

  text-align: center;
}

@media (max-width: 599px) {
  .popup__after-text {
    max-width: 220px;
    margin-bottom: 20px;

    font-size: 16px;
  }
}

.popup__after-close {
  justify-self: center;

  width: 100%;
  max-width: 360px;
  margin-top: 20px;
  padding: 16px;

  font-size: 28px;
}

@media (max-width: 599px) {
  .popup__after-close {
    max-width: 220px;
    padding: 12px;

    font-size: 20px;
  }
}
