.works {
  padding: 80px 0 74px;
  border-top: 10px solid #8abaf1;

  background-color: #212121;
}

@media (max-width: 479px) {
  .works {
    padding: 40px 0;
  }
}

.works__lead {
  margin: 0;
  margin-bottom: 20px;

  color: #fff;

  font-weight: 700;
  font-size: 36px;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: 479px) {
  .works__lead {
    font-size: 24px;
  }
}

.works__text {
  margin: 0;
  margin-bottom: 50px;

  color: #fff;

  font-size: 28px;
  text-align: center;
}

@media (max-width: 479px) {
  .works__text {
    margin-bottom: 35px;

    font-size: 18px;
  }
}

.works__swiper-container {
  position: relative;

  display: grid;

  margin: 0 auto 70px;
}

@media (max-width: 1439px) {
  .works__swiper-container {
    max-width: 930px;
  }
}

@media (max-width: 999px) {
  .works__swiper-container {
    max-width: 450px;
  }
}

@media (max-width: 479px) {
  .works__swiper-container {
    margin-bottom: 40px;
  }
}

.works__swiper-wrapper {
  position: relative;

  display: grid;
  grid-template-columns: repeat(100, 685px);
  grid-column-gap: 30px;

  margin: 0;
  padding: 0;

  list-style: none;
}

@media (max-width: 1439px) {
  .works__swiper-wrapper {
    grid-template-columns: repeat(100, 930px);
  }
}

@media (max-width: 989px) {
  .works__swiper-wrapper {
    grid-template-columns: repeat(100, 450px);
  }
}

@media (max-width: 509px) {
  .works__swiper-wrapper {
    grid-template-columns: 1fr;
    grid-auto-flow: column;
    grid-column-gap: 0;
  }
}

.works__swiper-slide {
  opacity: .4;

  transition: .2s;
}

.works__swiper-slide a {
  display: grid;
}

.works__swiper-slide img {
  width: 100%;
  border-radius: 4px;
}

.works__swiper-container .swiper-slide-active,
.works__swiper-container .swiper-slide-next {
  opacity: 1;
}

@media (max-width: 1439px) {
  .works__swiper-container .swiper-slide-next {
    opacity: .4;
  }
}

.works__swiper-buttons {
  position: absolute;
  top: 50%;
  left: -30px;

  display: grid;
  grid-template-columns: repeat(2, 60px);
  justify-content: space-between;

  width: 100vw;
  max-width: 1460px;

  pointer-events: none;

  transform: translateY(-50%);
}

@media (max-width: 1479px) {
  .works__swiper-buttons {
    left: 0;

    width: 100%;
  }
}

@media (max-width: 1439px) {
  .works__swiper-buttons {
    left: -30px;

    width: calc(100% + 60px);
  }
}

@media (max-width: 509px) {
  .works__swiper-buttons {
    left: 0;

    width: 100%;
  }
}

@media (max-width: 509px) {
  .works__swiper-buttons {
    left: -20px;

    grid-template-columns: repeat(2, 40px);

    width: calc(100% + 40px);
  }
}

.works__swiper-button {
  position: relative;

  width: 60px;
  height: 60px;

  padding: 0;
  border: none;

  background-color: transparent;

  cursor: pointer;
  pointer-events: visible;
}

@media (max-width: 509px) {
  .works__swiper-button {
    width: 40px;
    height: 40px;
  }
}

.works__link {
  display: grid;

  width: 100%;
  max-width: 450px;
  margin: 0 auto;
  padding: 30px;
}

@media (max-width: 459px) {
  .works__link {
    padding: 16px;

    font-size: 20px;
  }
}
