.include {
  padding-bottom: 74px;
}

.include__container {
  display: grid;
  justify-items: center;
}

.include__lead {
  margin: 0;
  margin-bottom: 50px;

  font-weight: 700;
  font-size: 48px;
  text-align: center;
  text-transform: uppercase;
}

.include__list {
  display: grid;
  grid-row-gap: 16px;

  width: 100%;
  max-width: 930px;

  margin: 0;
  padding: 0;

  list-style: none;
}

.include__item {
  display: grid;
  grid-template-columns: 161px 1fr;
  grid-column-gap: 40px;
  align-items: center;

  min-height: 134px;
  padding-right: 20px;
  border-radius: 4px;

  box-shadow: 0 0 19px 1px rgba(0, 0, 0, .14);
}

.include__image-wrap {
  position: relative;

  display: grid;

  align-items: center;
  justify-items: center;
  overflow: hidden;

  height: 100%;
}

.include__image-wrap::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;

  width: 161px;
  height: 348px;

  background-image: url("../img/include-item-bg-dc-blue.svg");
  background-size: cover;
  background-repeat: no-repeat;

  transition: .2s;
  transform: translateY(-50%);
}

.include__item:hover .include__image-wrap::before,
.include__item:focus .include__image-wrap::before {
  background-image: url("../img/include-item-bg-dc-gold.svg");
}

.include__image-wrap picture {
  position: relative;
}

.include__item-text {
  margin: 0;
  padding: 20px 0;

  font-size: 28px;
}

.include__item-text b {
  font-weight: 700;
  text-transform: uppercase;
}

@media (max-width: 1023px) {
  .include__lead {
    margin-bottom: 60px;

    font-size: 40px;
  }
}

@media (max-width: 799px) {
  .include__list {
    max-width: 670px;
  }

  .include__item {
    grid-template-columns: 1fr;
    grid-row-gap: 40px;

    padding-right: 0;
  }

  .include__image-wrap {
    height: 142px;
  }

  .include__image-wrap::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;

    width: 780px;
    height: 142px;

    background-image: url("../img/include-item-bg-dc-blue-mobile.svg");

    transform: translateX(-50%);
  }

  .include__item:hover .include__image-wrap::before,
  .include__item:focus .include__image-wrap::before {
    background-image: url("../img/include-item-bg-dc-gold-mobile.svg");
  }

  .include__item-text {
    padding: 0 16px 24px;

    text-align: center;
  }
}

@media (max-width: 579px) {
  .include__lead {
    margin-bottom: 46px;

    font-size: 36px;
  }

  .include__list {
    grid-row-gap: 26px;
  }

  .include__item-text {
    font-size: 24px;
  }
}

@media (max-width: 479px) {
  .include__lead {
    font-size: 24px;
  }
}

@media (max-width: 379px) {
  .include__item-text {
    font-size: 20px;
  }
}
