.nav {
  display: grid;
}

.nav__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

@media (min-width: 1024px) {
  .nav__close {
    display: none;
  }

  .nav__list {
    display: grid;
    grid-template-columns: repeat(7, max-content);
    grid-column-gap: 40px;
  }

  .nav__item {
    display: grid;
  }

  .nav__link {
    position: relative;

    padding: 24px 0;

    color: #fff;

    font-size: 24px;
    text-decoration: none;

    transition: .2s;
  }

  .nav__link:hover,
  .nav__link:focus {
    color: #fe0;
  }

  .nav__link--active {
    color: #fe0;
  }

  .nav__link--active::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    height: 2px;

    background-color: #fe0;
  }
}

@media (max-width: 1023px) {
  .nav--closed {
    display: none;
  }

  .nav--opened {
    position: absolute;
    top: 0;
    left: 0;

    z-index: 10;

    display: grid;
    align-items: start;
    overflow: hidden;

    width: 100vw;
    min-width: 320px;
    max-width: 450px;
    padding: 35px 20px 0;

    /* height: 100%; */

    /* min-height: 100%; */

    background-color: #212121;
  }

  .nav__close {
    display: flex;
    align-items: center;
    justify-content: center;
    justify-self: start;

    margin-bottom: 35px;

    padding: 0;
    border: none;

    background-color: transparent;

    cursor: pointer;
  }

  .nav__item {
    position: relative;

    display: grid;
  }

  .nav__item::before {
    content: "";
    position: absolute;
    top: 0;
    left: -20px;

    width: 100vw;
    min-width: 320px;
    height: 2px;

    background-color: #fff.1;
  }

  .nav__link {
    padding: 26px 0;

    color: #fff;

    font-size: 24px;
    text-decoration: none;
  }
}

@media (max-width: 330px) {
  .nav--opened {
    padding-top: 22px;
  }
}
