.reviews {
  display: grid;

  padding: 80px 0;
  border-bottom: 10px solid #8abaf1;

  background-color: #212121;
}

@media (max-width: 479px) {
  .reviews {
    padding: 40px 0;
  }
}

.reviews__container {
  display: grid;
  justify-items: center;
}

.reviews__lead {
  margin: 0;
  margin-bottom: 40px;

  color: #fff;

  font-weight: 700;
  font-size: 36px;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: 479px) {
  .reviews__lead {
    margin-bottom: 20px;

    font-size: 24px;
  }
}

.reviews__text {
  max-width: 1000px;
  margin: 0;
  margin-bottom: 50px;

  color: #fff;

  font-size: 28px;
  text-align: center;
}

@media (max-width: 479px) {
  .reviews__text {
    margin-bottom: 40px;

    font-size: 18px;
  }
}

.reviews__swiper-container {
  position: relative;

  display: grid;

  width: 100vw;
  max-width: 1400px;
  margin: 0 auto;
  margin-bottom: 60px;
}

@media (max-width: 1479px) {
  .reviews__swiper-container {
    max-width: 872px;
  }
}

@media (max-width: 952px) {
  .reviews__swiper-container {
    max-width: 453px;
  }
}

@media (max-width: 479px) {
  .reviews__swiper-container {
    margin-bottom: 40px;
  }
}

.reviews__swiper-wrapper {
  position: relative;

  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: column;

  margin: 0;
  padding: 0;

  list-style: none;
}

.reviews__swiper-slide {
  display: grid;
  grid-template-columns: max-content 1fr;
  grid-column-gap: 50px;
  align-items: center;

  padding: 112px 74px;

  background-color: #fff;

  opacity: .4;
}

@media (max-width: 1479px) {
  .reviews__swiper-slide {
    padding: 50px 60px;
  }
}

@media (max-width: 952px) {
  .reviews__swiper-slide {
    grid-template-columns: 1fr;
    justify-items: center;

    padding: 40px 24px;
  }
}

.reviews__swiper-wrapper .swiper-slide-active {
  opacity: 1;

  transition: .8s;
}

.reviews__image-wrap {
  width: 296px;
  height: 296px;
}

@media (max-width: 1479px) {
  .reviews__image-wrap {
    width: 196px;
    height: 196px;
  }
}

@media (max-width: 952px) {
  .reviews__image-wrap {
    margin-bottom: 20px;
  }
}

.reviews__blockqute-wrap {
  max-width: 880px;
}

@media (max-width: 1479px) {
  .reviews__blockqute-wrap {
    max-width: 504px;
  }
}

@media (max-width: 952px) {
  .reviews__blockqute-wrap {
    display: grid;
    justify-items: center;
  }
}

.reviews__ratings {
  display: grid;
  grid-template-columns: repeat(5, max-content);
  grid-column-gap: 8px;

  margin-bottom: 28px;
  padding: 0;

  list-style: none;
}

.reviews__rating {
  color: #d3d3d3;
}

.reviews__rating-icon {
  color: currentColor;
}

.reviews__blockqute-wrap blockquote {
  display: grid;

  margin: 0;
}

@media (max-width: 952px) {
  .reviews__blockqute-wrap blockquote {
    justify-items: center;

    text-align: center;
  }
}

.reviews__blockqute-wrap h3 {
  margin: 0;
  margin-bottom: 20px;

  font-size: 28px;
  font-family: 700px;
  text-transform: uppercase;
}

@media (max-width: 1479px) {
  .reviews__blockqute-wrap h3 {
    font-size: 24px;
  }
}

@media (max-width: 479px) {
  .reviews__blockqute-wrap h3 {
    font-size: 20px;
  }
}

.reviews__blockqute-wrap p {
  margin: 0;
  margin-bottom: 20px;

  font-size: 28px;
}

@media (max-width: 1479px) {
  .reviews__blockqute-wrap p {
    font-size: 24px;
  }
}

@media (max-width: 479px) {
  .reviews__blockqute-wrap p {
    font-size: 16px;
  }
}

.reviews__blockqute-wrap cite {
  margin: 0;

  font-size: 24px;

  opacity: .6;
}

@media (max-width: 1479px) {
  .reviews__blockqute-wrap cite {
    font-size: 20px;
  }
}

@media (max-width: 952px) {
  .reviews__blockqute-wrap cite {
    max-width: 300px;
  }
}

@media (max-width: 479px) {
  .reviews__blockqute-wrap cite {
    max-width: 240px;

    font-size: 18px;
  }
}

.reviews__swiper-buttons {
  position: absolute;
  top: 50%;
  left: -30px;

  display: grid;
  grid-template-columns: repeat(2, 60px);
  justify-content: space-between;

  width: 100vw;
  max-width: 1460px;

  pointer-events: none;

  transform: translateY(-50%);
}

@media (max-width: 1479px) {
  .reviews__swiper-buttons {
    max-width: 932px;
  }
}

@media (max-width: 952px) {
  .reviews__swiper-buttons {
    max-width: 513px;
    margin-top: -120px;
  }
}

@media (max-width: 509px) {
  .reviews__swiper-buttons {
    left: 0;

    grid-template-columns: repeat(2, 40px);

    width: 100%;
    margin-top: -124px;
  }
}

@media (max-width: 479px) {
  .reviews__swiper-buttons {
    margin-top: -66px;
  }
}

.reviews__swiper-button {
  position: relative;

  width: 60px;
  height: 60px;

  padding: 0;
  border: none;
  border-radius: 50%;

  background-color: transparent;
  box-shadow: 0 8px 44px 2px rgba(0, 0, 0, .31);

  cursor: pointer;
  pointer-events: visible;
}

@media (max-width: 509px) {
  .reviews__swiper-button {
    width: 40px;
    height: 40px;
  }
}

.reviews__link {
  position: relative;

  justify-self: center;

  width: 100%;
  max-width: 690px;
  padding: 34px 20px 26px;

  font-weight: 500;
  font-size: 28px;
}

@media (max-width: 729px) {
  .reviews__link {
    width: calc(100% - 40px);
    padding: 30px 20px;
  }
}

@media (max-width: 479px) {
  .reviews__link {
    padding: 16px;

    font-size: 20px;
  }
}

.reviews__link::before {
  content: url("../img/reviews-google-pin.png");
  position: absolute;
  top: 50%;
  left: 50%;

  margin-left: -280px;

  transform: translate(-50%, -50%);
}

@media (max-width: 729px) {
  .reviews__link::before {
    display: none;
  }
}
